h1 {
  position:relative;
  font-family: 'Bebas Neue', cursive;
    font-style: bold;
    font-size: 9vw;
    color:  #e2098e;
    border-top: 3px dashed #e2098e;
    border-bottom:3px dashed #e2098e;
    padding:10px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
h1:before {
 content:"";
 position: absolute;
 top:5px;
 bottom:5px;
 left:0;
 right:0;
 border-top: 3px dashed #e2098e;
 border-bottom:3px dashed #e2098e;
}
h1:after {
 content:"";
 position: absolute;
 top:15px;
 bottom:15px;
 left:0;
 right:0;
 border-top: 3px dashed #e2098e;
 border-bottom:3px dashed #e2098e;
}
